// Proper ZIP file creation using JSZip library
const createZipFile = async (files: { name: string; content: string }[]): Promise<Blob> => {
    const zip = new JSZip();

    // Add each file to the zip
    files.forEach(file => {
        zip.file(file.name, file.content);
    });

    // Generate the zip file as a blob
    return await zip.generateAsync({ type: 'blob' });
};

// Function to fetch entire scene folder contents
const fetchSceneContents = async (sceneName: string, dirPath: string, folderInZip: JSZip): Promise<void> => {
    // Define file patterns in smaller chunks to avoid buffer issues
    const fileGroups = [
        // Common scene files
        ['README.md', 'scene.xml', 'container.xml', 'mannequin.xml'],

        // Asset folder files
        ['asset/README.md', 'asset/bunny.obj', 'asset/bunny_with_uv.obj', 'asset/cap.obj', 'asset/sponge.png', 'asset/spot.obj', 'asset/spot.png'],

        // Humanoid and misc files
        ['humanoid.png', 'humanoid.xml', 'bunny.obj', 'assets/card.obj'],

        // Subdirectory XML files (part 1)
        ['bowl/bowl.mjcf.xml', 'bowl/scene.xml', 'bunnies/bunnies.mjcf.xml', 'bunnies/bunny.obj', 'bunnies/container.xml'],

        // Subdirectory XML files (part 2)
        ['cow/cow.mjcf.xml', 'cow/scene.xml', 'cow/asset/spot.obj', 'cow/asset/spot.png', 'cylinder/cylinder.mjcf.xml'],

        // Subdirectory XML files (part 3)
        ['flag/flag.mjcf.xml', 'floppy/floppy.mjcf.xml', 'floppy/scene.xml', 'gear/gear.mjcf.xml', 'gear/scene.xml'],

        // Subdirectory XML files (part 4)
        ['hammock/hammock.mjcf.xml', 'hammock/humanoid.png', 'hammock/humanoid.xml', 'helix/helix.mjcf.xml'],

        // Subdirectory XML files (part 5)
        ['jelly/jelly.mjcf.xml', 'jelly/scene.xml', 'leaves/leaves.mjcf.xml', 'mug/mug.mjcf.xml', 'mug/scene.xml'],

        // Subdirectory XML files (part 6)
        ['newton_cradle/newton_cradle.mjcf.xml', 'nutbolt/nutbolt.mjcf.xml', 'nutbolt/scene.xml'],

        // Subdirectory XML files (part 7)
        ['particle/particle.mjcf.xml', 'particle/scene.xml', 'poncho/mannequin.xml', 'poncho/poncho.mjcf.xml'],

        // Subdirectory XML files (part 8)
        ['pulley/pulley.mjcf.xml', 'pulley/scene.xml', 'stonehenge/stonehenge.mjcf.xml', 'torus/scene.xml', 'torus/torus.mjcf.xml'],

        // Card assets (numbers 2-5)
        ['assets/2_of_clubs.png', 'assets/2_of_diamonds.png', 'assets/2_of_hearts.png', 'assets/2_of_spades.png',
         'assets/3_of_clubs.png', 'assets/3_of_diamonds.png', 'assets/3_of_hearts.png', 'assets/3_of_spades.png',
         'assets/4_of_clubs.png', 'assets/4_of_diamonds.png', 'assets/4_of_hearts.png', 'assets/4_of_spades.png',
         'assets/5_of_clubs.png', 'assets/5_of_diamonds.png', 'assets/5_of_hearts.png', 'assets/5_of_spades.png'],

        // Card assets (numbers 6-9)
        ['assets/6_of_clubs.png', 'assets/6_of_diamonds.png', 'assets/6_of_hearts.png', 'assets/6_of_spades.png',
         'assets/7_of_clubs.png', 'assets/7_of_diamonds.png', 'assets/7_of_hearts.png', 'assets/7_of_spades.png',
         'assets/8_of_clubs.png', 'assets/8_of_diamonds.png', 'assets/8_of_hearts.png', 'assets/8_of_spades.png',
         'assets/9_of_clubs.png', 'assets/9_of_diamonds.png', 'assets/9_of_hearts.png', 'assets/9_of_spades.png'],

        // Card assets (10s and face cards)
        ['assets/10_of_clubs.png', 'assets/10_of_diamonds.png', 'assets/10_of_hearts.png', 'assets/10_of_spades.png',
         'assets/jack_of_clubs.png', 'assets/jack_of_diamonds.png', 'assets/jack_of_hearts.png', 'assets/jack_of_spades.png',
         'assets/queen_of_clubs.png', 'assets/queen_of_diamonds.png', 'assets/queen_of_hearts.png', 'assets/queen_of_spades.png'],

        // Card assets (kings, aces, jokers)
        ['assets/king_of_clubs.png', 'assets/king_of_diamonds.png', 'assets/king_of_hearts.png', 'assets/king_of_spades.png',
         'assets/ace_of_clubs.png', 'assets/ace_of_diamonds.png', 'assets/ace_of_hearts.png', 'assets/ace_of_spades.png',
         'assets/black_joker.png', 'assets/red_joker.png']
    ];

    // Process each group of files
    for (const fileGroup of fileGroups) {
        for (const fileName of fileGroup) {
            try {
                const response = await fetch(`${dirPath}/${fileName}`);
                if (response.ok) {
                    if (fileName.endsWith('.png') || fileName.endsWith('.jpg') || fileName.endsWith('.obj') || fileName.endsWith('.mtl') || fileName.endsWith('.stl')) {
                        const blob = await response.blob();
                        folderInZip.file(fileName, blob);
                    } else {
                        const content = await response.text();
                        folderInZip.file(fileName, content);
                    }
                    console.log(`Added ${fileName} to ${sceneName} zip`);
                }
            } catch (e) {
                // File doesn't exist, skip silently
            }
        }
    }
};

// Function to create and download scene folder as zip
const downloadSceneZip = async (sceneName: string, scenePath: string): Promise<void> => {
    try {
        const zip = new JSZip();

        // Create a folder for the scene
        const sceneFolder = zip.folder(sceneName);

        if (!sceneFolder) {
            throw new Error('Failed to create scene folder in zip');
        }

        // Extract directory path and filename
        const fullPath = `/assets/scenes/${scenePath}`;
        const pathParts = scenePath.split('/');
        const mjcfFileName = pathParts[pathParts.length - 1];
        const sceneDir = pathParts.slice(0, -1).join('/');
        const sceneDirPath = `/assets/scenes/${sceneDir}`;

        // Fetch the main MJCF file
        const mjcfResponse = await fetch(fullPath);
        if (mjcfResponse.ok) {
            const mjcfContent = await mjcfResponse.text();
            sceneFolder.file(mjcfFileName, mjcfContent);
        }

        // Fetch all other files in the scene directory
        await fetchSceneContents(sceneName, sceneDirPath, sceneFolder);

        // Generate the zip file
        const zipBlob = await zip.generateAsync({
            type: 'blob',
            compression: 'DEFLATE',
            compressionOptions: {
                level: 6
            }
        });

        // Download the zip
        const url = URL.createObjectURL(zipBlob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `${sceneName}_scene.zip`;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);

    } catch (error) {
        console.error('Failed to create scene zip:', error);
        throw error;
    }
};

